export default function OSP0112() {
  const [tabNumber, setTabNumber] = useState(0);

  const page = {
    code: 8,
    subcode: 0,
    community: true,
    inputCate: {
      banner: false,
      name: "search-table",
      placeHolder: '버전 검색'
    }
  };

  const tos = [
    {
      name: 'TmaxGooroom',
      ver: 'ver 3.0',
      size: '1.5GB'
    },
    {
      name: 'TmaxGooroom',
      ver: 'ver 2.4',
      size: '1.5GB'
    },
    {
      name: 'TmaxGooroom',
      ver: 'ver 2.3',
      size: '1.5GB'
    },
  ];

  // tab에 따라서 다른 컴포넌트를 보여준다. 
  const toggleContents = (tabNumber: number) => {
    switch (tabNumber) {
      case 0:
        return <SectionOSP0112 tos={tos} page={page} />;
      case 1:
        return <MainOSP0113 />;
      default:
        return <>
          잘못된 접근
          history 관리하기
        </>;
    }
  }

  const onClick = (event) => {
    const { target: { value } } = event;
    if (value) {
      setTabNumber(Number(value));
    }
  }


  return <>
    <Banner page={page} />
    <TabBarStyle props={{
      list: [
        'Tmax구름 다운로드', '소스 코드'
      ],
      commu: true,
      tabNumber,
      setTabNumber
    }}
    />


    {toggleContents(tabNumber)}
  </>
}